}

.agenda {
  width: min(90%, 140rem);
  margin: 0 auto;
}
.agenda__heading {
  color: #007df4;
  margin: 5rem 0;
  text-align: left;
  font-size: clamp(2rem, 5vw, 5rem);
}
.agenda__description {
  color: #64748B;
  margin: 2rem 0;
  text-align: left;
  font-size: clamp(1rem, 2vw, 1.5rem);
}
.agenda__button {
  width: 50%;
  padding: 10px;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  font-size: 16px;
  transition: all 0.3s;
}
.agenda__button:hover {
  background-color: #00a8ff;
}
.agenda__input {
  width: 100%;
  padding: 10px;
  border: 2px solid #ccc;
  border-radius: 20px;
  font-size: 16px;
  outline: none;
}

.sitios__grid {
  width: min(90%, 140rem);
  margin: 0 auto;
  display: grid;
  gap: 5rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .sitios__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1200px) {
  .sitios__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(4, 1fr);
    align-items: flex-start;
  }
}
.sitios__heading {
  color: #007df4;
  margin: 5rem 0;
  text-align: left;
  font-size: clamp(2rem, 5vw, 5rem);
}

.resumen {
  background-image: linear-gradient(to right, rgba(0, 125, 244, 0.7), rgba(0, 125, 244, 0.9)), url(../img/header.jpg);
  background-image: linear-gradient(to right, rgba(0, 125, 244, 0.7), rgba(0, 125, 244, 0.9)), image-set(url("../img/header.avif") 1x, url("../img/header.webp") 1x, url("../img/header.jpg") 1x);
  padding: 2rem 0;
  background-size: cover;
  background-position: center;
}
.resumen__grid {
  width: min(90%, 140rem);
  margin: 0 auto;
  display: grid;
  gap: 5rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .resumen__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.resumen__bloque {
  text-align: center;
  color: #FFFFFF;
}
.resumen__texto, .resumen__texto--numero {
  font-weight: 900;
  line-height: 1;
  margin: 0;
  font-size: 2.4rem;
  text-transform: uppercase;
}
.resumen__texto--numero {
  font-weight: 900;
  line-height: 1;
  margin: 0;
  font-size: 2.4rem;
}

.empresas__grid {
  width: min(90%, 140rem);
  margin: 0 auto;
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .empresas__grid {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1200px) {
  .empresas__grid {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(3, 1fr);
    align-items: flex-start;
  }
}
.empresas__heading, .empresas__descripcion {
  text-align: center;
  margin-bottom: 1rem;
}

.empresa {
  border: 0.5px solid #bac2ce;
  padding: 3rem;
  position: relative;
  border-radius: 0.5rem;
  overflow: hidden;
  transition: transform 0.3s ease, filter 0.3s ease;
}
.empresa__nombre {
  font-weight: 900;
  font-size: 2.6rem;
  margin: 2rem 0;
}
.empresa__imagen {
  width: 100%;
  height: auto;
  max-height: 150px;
  object-fit: cover;
  border-radius: 0.5rem;
  filter: brightness(0.85);
  transition: transform 0.3s ease;
}
.empresa__informacion {
  position: relative;
  z-index: 1;
  padding: 1rem;
  background-color: rgba(88, 155, 255, 0.219);
}
.empresa__informacion__nombre, .empresa__informacion__ubicacion {
  color: #fff;
}
.empresa__informacion__nombre {
  font-size: 1.25rem;
  font-weight: bold;
  margin-bottom: 0.25rem;
}
.empresa__informacion__ubicacion {
  font-size: 0.875rem;
}
.empresa:hover {
  transform: scale(1.02);
  filter: brightness(0.9);
}
.empresa:hover .empresa__imagen {
  transform: scale(1.05);
}

.mapa {
  height: 40rem;
  margin-top: 4rem;
}
.mapa__heading {
  margin: 0;
  text-align: left;
  font-size: 2rem;
}
.mapa__texto {
  font-size: 1.6rem;
  margin: 0;
}

.boletos__grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .boletos__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(1, 1fr);
  }
}
@media (min-width: 1024px) {
  .boletos__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.boleto {
  height: auto;
  width: min(100% - 5rem, 65rem);
  margin: 0 auto;
  padding: 6rem;
  border-radius: 2rem;
  color: #FFFFFF;
  position: relative;
}
.boleto::before, .boleto::after {
  position: absolute;
  content: "";
  background-color: #FFFFFF;
  width: 5rem;
  height: 5rem;
  top: calc(50% - 2.5rem);
}
.boleto::before {
  clip-path: circle(50% at 25% 50%);
  left: 0;
}
.boleto::after {
  clip-path: circle(50% at 75% 50%);
  right: 0;
}
.boleto--presencial {
  background-color: hsl(178, 100%, 39%);
  background-image: radial-gradient(at 40% 20%, hsl(209, 100%, 47%) 0px, transparent 50%), radial-gradient(at 80% 0%, hsl(178, 100%, 39%) 0px, transparent 50%), radial-gradient(at 69% 82%, hsl(209, 100%, 47%) 0px, transparent 50%), radial-gradient(at 26% 91%, hsl(178, 100%, 39%) 0px, transparent 50%), radial-gradient(at 0% 100%, hsl(209, 100%, 47%) 0px, transparent 50%), radial-gradient(at 27% 22%, hsl(178, 100%, 39%) 0px, transparent 50%), radial-gradient(at 0% 0%, hsl(209, 100%, 47%) 0px, transparent 50%);
}
.boleto--virtual {
  background-color: #00c8c2;
}
.boleto--gratis {
  background-color: #007df4;
}
.boleto__logo {
  color: #FFFFFF;
  text-align: center;
  margin-bottom: 2rem;
  white-space: nowrap;
  font-size: clamp(1rem, -0.8rem + 10vw, 2rem);
  font-size: 2rem;
}
@media (min-width: 1024px) {
  .boleto__logo {
    text-align: left;
    margin: 0;
  }
}
@media (min-width: 480px) {
  .boleto__logo {
    font-size: 4rem;
  }
}
.boleto__plan {
  font-size: 2rem;
  text-transform: uppercase;
  margin: 3rem 0 0 0;
  text-align: right;
}
.boleto__precio {
  margin: 3rem 0 0 0;
  font-weight: 900;
  font-size: 4rem;
  text-align: right;
}
.boleto__enlace-contenedor {
  margin-top: 2rem;
  display: flex;
  justify-content: center;
}
.boleto__enlace {
  background-color: #007df4;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  width: auto;
}
.boleto__enlace:hover {
  background-color: #0063c1;
  cursor: pointer;
}
.boleto--acceso {
  width: 30rem;
  height: 50rem;
  padding: 10rem 3rem;
}
@media (min-width: 768px) {
  .boleto--acceso {
    width: 60rem;
    height: 30rem;
    padding: 12rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}
.boleto--acceso .boleto__logo {
  font-size: clamp(2rem, -0.8rem + 10vw, 3rem);
  margin: 0;
  text-align: left;
}
.boleto--acceso .boleto__nombre {
  font-size: 2.4rem;
  text-transform: uppercase;
  font-weight: 900;
}
.boleto--acceso .boleto__codigo {
  font-size: 4rem;
  font-weight: 700;
  text-align: center;
  position: absolute;
  bottom: 7rem;
  margin: 0;
  left: 0;
  width: 100%;
}
@media (min-width: 768px) {
  .boleto--acceso .boleto__codigo {
    rotate: 90deg;
    transform-origin: 0 0;
    top: 0;
    left: calc(100% - 5rem);
    width: 30rem;
  }
}
.boleto::before {
  clip-path: circle(50% at 50% 25%);
  left: calc(50% - 2.5rem);
  top: 0;
}
@media (min-width: 768px) {
  .boleto::before {
    clip-path: circle(50% at 25% 50%);
    left: 0;
    top: calc(50% - 2.5rem);
  }
}
.boleto::after {
  clip-path: circle(50% at 50% 75%);
  left: calc(50% - 2.5rem);
  top: calc(100% - 5rem);
}
@media (min-width: 768px) {
  .boleto::after {
    clip-path: circle(50% at 75% 50%);
    left: calc(100% - 5rem);
    top: calc(50% - 2.5rem);
  }
}

